import { GlobalContext } from 'contents/MainFrame';
import { useContext } from 'react';
import './UserPlate.css';

const UserPlate = (
    props: {
    }
) => {
    const { state, dispatcher } = useContext(GlobalContext);
    return (
        <div id="user-plate" className={[''].join(' ')}>
            <div className="user-name">
                <span>{state.login.user}</span>
            </div>
            <div 
            className="logout"
            
            onClick={() => {

                dispatcher.setLoginParams({
                    ...state.login,
                    isLogin: false,
                    user: ''
                });
            }}>
                <span>LOGOUT</span>
            </div>
        </div>
    );
}

export default UserPlate;